import Vue from 'vue'
import { requestLogout, requestSetOpen } from '@/api/API'
import Header from '../../components/Header'
import { Cell, Switch } from 'mint-ui'
Vue.component(Cell.name, Cell)
Vue.component(Switch.name, Switch)
export default {
  name: 'setting',
  components: {
    Header
  },
  data () {
    return {
      isOpen: false,
      user: {}
    }
  },
  mounted () {
    this.user = this.$store.state.User.user || {}
    this.isOpen = this.user.is_open === '1'
  },
  methods: {
    async changeOpen () {
      let user = this.$store.state.User || {}
      let is_open = 0
      if (this.isOpen) {
        is_open = 1
      }
      let res = await requestSetOpen(user.token, is_open)
      if (res.code === 200) {
        this.user.is_open = is_open
        this.$store.commit('User/updateUser', this.user)
      } else {
        this.isOpen = false
      }
      window.EventBus.$emit('globalMessage', res.msg)
    },
    async onLogout () {
      let userObj = this.$store.state.User || {}
      let res = await requestLogout(userObj.token, userObj.uuid)
      this.$store.commit('User/updateToken', null)
      window.EventBus.$emit('globalMessage', res.msg)
      this.$router.replace('profile')
    }
  }
}



